.App {
  text-align: left;
}
#page-content {
  width: 75%;
  margin: 0 auto;
}

#navbar {
  width: 100%;
  margin: 0 auto;
  min-height: 50px;
}
#navbar ul {
  display: flex;
  justify-content: space-around;
  list-style: none;

  padding: 0;
  margin: 0;

  width: 100%;
  height: 50px;
}
#navbar ul li {
  width: 100%;
  height: 100%;
  text-align: center;

  background-color: rgb(41, 41, 41);
  border: solid rgb(41, 41, 41) 2px;
}
#navbar a {
  text-decoration: none;
  color: white;
  display: block;

  line-height: 45px;
  height: 100%;
  background-color: rgb(41, 41, 41);
}
#navbar a:hover {
  background-color: white;
  color: rgb(41, 41, 41);
  transition: all 0.3s;
}

.comment-section {
  background-color: rgba(202, 202, 202, 0.5);

  padding: 5px;
  border-radius: 10px;
  margin-bottom: 10px;
}
.comment-section > h4 {
  font-size: 1.4em;
  margin: 5px 20px 12px 20px;
}
.comment-section .comment {
  background-color: rgba(202, 202, 202, 0.5);

  padding: 1px 10px;
  margin: 10px 0;

  border-radius: 3px;
}
.comment-section .comment > * {
  margin: 2px 0;
}
.comment-section textarea {
  background-color: transparent;
  resize: none;
  border-radius: 5px;
  display: block;
  margin-bottom: 10px;
}
.comment-section input {
  background-color: transparent;
  resize: none;
  border-radius: 5px;
  display: block;
  margin-bottom: 10px;
}
.comment-section button {
  background-color: rgb(73, 119, 180);
  color: white;
  padding: 3px 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.article-list {
  margin-bottom: 30px;
}
.article-list a {
  text-decoration: none;
  color: rgb(63, 63, 63);

  display: block;
  margin-bottom: 30px;
}
.article-list p {
  border-bottom: solid black 2px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
